/**
 * éŸ³éŸ¿å‡¦ç†ã«æœ€é©åŒ–ã•ã‚ŒãŸãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹åˆ¶å¾¡
 * éŸ³è³ªã‚’ç¶­æŒã—ãªãŒã‚‰CPUè² è·ã‚’å‰Šæ¸›
 */

export class AdaptiveFrameRateLimiter {
  private lastFrameTime = 0;
  private nextFrameTime = 0;
  private targetFPS: number;
  private frameInterval: number;
  private frameDrops = 0;
  
  // éŸ³æ¥½ç”¨é€”ã«é©ã—ãŸç¯„å›²
  private readonly MIN_FPS = 30;  // æœ€ä½30FPSï¼ˆ33msä»¥ä¸‹ã®ãƒ¬ã‚¤ãƒ†ãƒ³ã‚·ãƒ¼ï¼‰
  private readonly MAX_FPS = 60;  // æœ€é«˜60FPSï¼ˆãƒ–ãƒ©ã‚¦ã‚¶æ¨™æº–ï¼‰
  private readonly OPTIMAL_FPS = 45; // æ¨å¥¨45FPSï¼ˆ22msã€éŸ³æ¥½æ¼”å¥ã«é©åˆ‡ï¼‰
  
  constructor(initialFPS = 45) {
    this.targetFPS = Math.max(this.MIN_FPS, Math.min(initialFPS, this.MAX_FPS));
    this.frameInterval = 1000 / this.targetFPS;
  }
  
  shouldProcess(): boolean {
    const now = performance.now();
    
    // åˆå›å®Ÿè¡Œæ™‚ã®åˆæœŸåŒ–
    if (this.nextFrameTime === 0) {
      this.nextFrameTime = now + this.frameInterval;
      this.lastFrameTime = now;
      return true;
    }
    
    // æ¬¡ãƒ•ãƒ¬ãƒ¼ãƒ æ™‚åˆ»ã«åˆ°é”ã—ãŸã‹ãƒã‚§ãƒƒã‚¯
    if (now >= this.nextFrameTime) {
      const actualElapsed = now - this.lastFrameTime;

      // ãƒ•ãƒ¬ãƒ¼ãƒ è½ã¡ã‚’æ¤œå‡ºï¼ˆæœŸå¾…é–“éš”ã®2.0å€ã‚’è¶…éï¼‰
      // 1.5å€â†’2.0å€ã«ç·©å’Œ: ä¸€æ™‚çš„ãªè² è·å¤‰å‹•ã‚’è¨±å®¹ã—ã€å®‰å®šæ€§ã‚’å‘ä¸Š
      if (actualElapsed > this.frameInterval * 2.0) {
        this.frameDrops++;
        this.adjustFrameRate();
      }
      
      // æ¬¡ãƒ•ãƒ¬ãƒ¼ãƒ æ™‚åˆ»ã‚’çµ¶å¯¾æ™‚åˆ»ã§è¨­å®šï¼ˆç´¯ç©èª¤å·®ã‚’å›é¿ï¼‰
      this.nextFrameTime = now + this.frameInterval;
      this.lastFrameTime = now;
      
      return true;
    }
    
    return false;
  }
  
  // CPUè² è·ã«å¿œã˜ã¦å‹•çš„ã«FPSã‚’èª¿æ•´
  private adjustFrameRate(): void {
    if (this.frameDrops > 5 && this.targetFPS > this.MIN_FPS) {
      // è² è·ãŒé«˜ã„å ´åˆã¯FPSã‚’ä¸‹ã’ã‚‹
      this.targetFPS = Math.max(this.MIN_FPS, this.targetFPS - 5);
      this.frameInterval = 1000 / this.targetFPS;
      this.frameDrops = 0;
      
      // æ¬¡ãƒ•ãƒ¬ãƒ¼ãƒ æ™‚åˆ»ã‚’æ–°ã—ã„é–“éš”ã§å†è¨ˆç®—
      const now = performance.now();
      this.nextFrameTime = now + this.frameInterval;
      
      console.log(`Adjusted FPS to ${this.targetFPS} due to high load`);
    }
  }
  
  // ãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹å›å¾©æ™‚ã«FPSã‚’æˆ»ã™
  recoverPerformance(): void {
    if (this.frameDrops === 0 && this.targetFPS < this.OPTIMAL_FPS) {
      this.targetFPS = Math.min(this.OPTIMAL_FPS, this.targetFPS + 5);
      this.frameInterval = 1000 / this.targetFPS;
      
      // æ¬¡ãƒ•ãƒ¬ãƒ¼ãƒ æ™‚åˆ»ã‚’æ–°ã—ã„é–“éš”ã§å†è¨ˆç®—
      const now = performance.now();
      this.nextFrameTime = now + this.frameInterval;
    }
  }
  
  reset(): void {
    this.lastFrameTime = 0;
    this.nextFrameTime = 0;
    this.frameDrops = 0;
    this.targetFPS = this.OPTIMAL_FPS;
    this.frameInterval = 1000 / this.targetFPS;
  }
  
  getStats() {
    return {
      currentFPS: this.targetFPS,
      frameDrops: this.frameDrops,
      latency: this.frameInterval
    };
  }
}

/**
 * éŸ³éŸ¿å‡¦ç†å°‚ç”¨ã®ã‚¹ãƒ­ãƒƒãƒˆãƒªãƒ³ã‚°
 * è¦–è¦šæ›´æ–°ã¨éŸ³å£°å‡¦ç†ã‚’åˆ†é›¢
 */
export class AudioProcessingThrottle {
  private visualUpdateThrottle: number;
  private audioProcessingThrottle: number;
  
  constructor() {
    // è¦–è¦šæ›´æ–°ã¯30FPSï¼ˆ33msï¼‰ã§ååˆ†
    this.visualUpdateThrottle = 33;
    
    // éŸ³å£°å‡¦ç†ã¯é«˜é »åº¦ã‚’ç¶­æŒï¼ˆ60FPSç›¸å½“ï¼‰
    this.audioProcessingThrottle = 16;
  }
  
  // UIæ›´æ–°ç”¨ï¼ˆãƒ¡ãƒ¼ã‚¿ãƒ¼ã€ã‚°ãƒ©ãƒ•ãªã©ï¼‰
  throttleVisual<T extends (...args: any[]) => any>(
    func: T,
    customWait?: number
  ): (...args: Parameters<T>) => void {
    const wait = customWait || this.visualUpdateThrottle;
    return throttle(func, wait, { leading: true, trailing: true });
  }
  
  // éŸ³å£°å‡¦ç†ç”¨ï¼ˆãƒ”ãƒƒãƒæ¤œå‡ºãªã©ï¼‰
  throttleAudio<T extends (...args: any[]) => any>(
    func: T,
    customWait?: number
  ): (...args: Parameters<T>) => void {
    const wait = customWait || this.audioProcessingThrottle;
    return throttle(func, wait, { leading: true, trailing: false });
  }
}

// æ—¢å­˜ã®throttleé–¢æ•°ã‚’å†åˆ©ç”¨
function throttle<T extends (...args: any[]) => any>(
  func: T,
  wait: number,
  options: { leading?: boolean; trailing?: boolean } = {}
): (...args: Parameters<T>) => void {
  let timeout: NodeJS.Timeout | null = null;
  let previous = 0;
  let lastArgs: Parameters<T> | null = null;
  
  const { leading = true, trailing = true } = options;
  
  return function throttled(...args: Parameters<T>) {
    const now = Date.now();
    
    if (!previous && !leading) {
      previous = now;
    }
    
    const remaining = wait - (now - previous);
    lastArgs = args;
    
    if (remaining <= 0 || remaining > wait) {
      if (timeout) {
        clearTimeout(timeout);
        timeout = null;
      }
      previous = now;
      func.apply(null, args);
      lastArgs = null;
    } else if (!timeout && trailing) {
      timeout = setTimeout(() => {
        previous = leading ? Date.now() : 0;
        timeout = null;
        if (lastArgs) {
          func.apply(null, lastArgs);
          lastArgs = null;
        }
      }, remaining);
    }
  };
}